* {
  box-sizing: border-box;
}

.grid-container {
  width: 70%;
  display: grid;
  margin-top: 120px;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 10px;
}

.btn {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.btn button {
  border-style: none;
  padding: 2px 8px;
  margin-right: 10px;
  border-radius: 5px;
  background: #30a7d7;
  color: #fff;
  font-size: 1em;
}

.Navbar {
  width: 100%;
  padding: 1px;
  background: #ff4236;
  color: white;
  position: sticky;
  top: 0;
}

.Navbar p{
  display: flex;
  justify-content: center;
  padding-bottom: 1px;
}

.Search {
  padding-top: 0px;
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.Search p{
  padding-right: 10px;
}